.search {
  background-color: var(--warna-putih);
  margin-top: 3rem;
  display: flex;
  justify-content: left;
  align-items: center;
  border-radius: 50px;
  width: 300px;
}
@media screen and (min-width: 64rem) {
  .search {
    margin-top: 1rem;
  }
}
.search input {
  padding: 10px 50px 10px 5px;
  border: none;
  border-radius: 50px;
}
@media screen and (min-width: 64rem) {
  .search input {
    padding: 10px 70px 10px 5px;
    border: none;
    border-radius: 50px;
  }
}